.card {
    width: 225px;
    height: 420px;
    position: relative;
    margin: 50px;
    text-align: center;
    font-size: 1em;
    font-family: Georgia;
    perspective: 800px;
  }
  
  .left-align {
    text-align: left;
  }
  
  .front,
  .back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    transition: transform 1.2s;
    border: double 10px rgb(0, 0, 0);
    border-radius: 20px;
    padding: 10px;
  }
  
  .front {
    background-color: rgb(44, 44, 44);
    color: black;
    z-index: 3;
  }
  
  .back {
    background-image: url('https://cfb.rabbitloader.xyz/zpqk9g88/rls.t-nw-a28/wp-content/uploads/2022/08/Black-2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-color: rgb(41, 41, 41);
    color: black;
    transform: rotatey(180deg);
  }
  
  .card_picture {
   height: 44%;
  }
  
  .card_table {
    width: 100%;
    color: rgb(202, 165, 92);
    border-spacing: 10px;
  }
  
  .card:hover .front {
    transform: rotatey(-180deg);
  }
  
  .card:hover .back {
    transform: rotatey(0deg);
  }

  .card_name {
    color: rgb(202, 165, 92);
    font-size:large;
  }
   
  
  .button {
    background: rgb(52, 52, 52);
    color: white;
    width: 100px;
    text-align: center;
    padding: 10px;
    margin-top: 60px;
    margin-left: 110px;
    cursor: pointer;
   }
  
  .button:hover {
    background: black;
    transform: scaleY(1.05);
  }